




























































































































.messagebar {
  display: flex;
  align-items: center;
  &-area {
    position: relative;
    flex-grow: 1;
    margin-right: 20px;
  }
  &-input {
    background-color: rgba(white, 0.3);
    height: 40px;
    border-radius: 20px;
    border: none;
    width: 100%;
    box-sizing: border-box;
    padding: 6px 15px;
  }
  &-emoji {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
  &-action {
    flex-shrink: 0;
  }
}
.emoji {
  &-picker {
    padding: 10px 0;
  }

  &-item {
  }
}
